<template>
<div class="login-container bg-light">
        <b-row class=" w-100">
            <b-col sm="4" offset-sm="4">
                <b-card>
                    <b-form @submit.prevent="sentCode()">
                        <h1 class="mb-3 h3">Forgot Password</h1>
                        <b-input-group class="mt-3">
                            <template #prepend>
                            <b-input-group-text>
                                <CIcon name="cil-user"/>
                            </b-input-group-text>
                            </template>
                            <b-form-input v-model="info.email" required  type="email" placeholder="Email"></b-form-input>
                        </b-input-group>

                        
                        <!-- <b-input v-model="info.email" required  type="email" placeholder="Email" size="lg" class="mb-2"></b-input> -->
                        <!-- <b-input v-model="info.password" required  placeholder="Password" size="lg" type="password" class="mb-2"></b-input> -->
                        <div class="d-flex">
                            <b-button variant="success"  :disabled="showCount" type="submit" class="w-100 mt-3">
                                <span v-if="!loading.login && !showCount" >Send code</span>
                                <b-spinner v-if="loading.login" small type="grow"></b-spinner>
                                <span v-if="showCount">Resend Code {{count}}</span>
                            </b-button>
                        </div>
                        <b-alert class="mt-2 mb-0 text-center" variant="success" :show="showSuccess">Please Check Your Mail.</b-alert>
                    </b-form>
                </b-card>
            </b-col>
        </b-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'ForGotPAssword',
    data() {
        return {
            loading:{
                login:false
            },
            showSuccess:false,
            showCount:false,
            count:60,
            info:{
                email:"",
            }
        }
    },
    methods: {
        sentCode() {
            this.loading.login = true
            axios.post(`api/v1/password/send-email`, this.info)
            .then(res=>{
                this.showSuccess = true;
                this.loading.login = false
                this.countDown();
                this.count = 60
            })
            .catch(err=>{
                console.log(err)
                this.loading.login = false
            })
        },
        countDown() {
            this.showCount = true
            setInterval(()=>{
                
                if(this.count < 1) {
                    clearInterval(this.count)
                    this.showCount = false
                } else {
                    this.count--
                }
            },1000)
        }
    },
    mounted() {

    }
}
</script>
<style scoped>
.login-container{
    min-height: 100vh;
    display: flex;
    align-items: center;
} 

</style>